.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #4158D0;
  background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);

}

.auth-form-container, .login-form, .register-form{
  display: flex;
  flex-direction: column;
  
}
@media screen and (min-width: 600px) {
  .auth-form-container {
    padding: 5rem;
    border: 1px solid white;
    border-radius: 10px;
    margin: 0.5rem;
  }
}
label{
  padding: 0.05rem 0;
  text-align: left;
}
input{
  margin: 0.5rem 0;
  padding: 0.5rem;
  border: none;
  border-radius: 10px;
}
button{
  border: none;
  background-color: white;
  border-radius: 10px;
  margin-top: 0.50rem;
  padding: 10px;
  cursor: pointer;
  color: #7439db;
}
.link-btn {
  background: none;
  color: white;
  text-decoration: underline;
}